:root {
  --color-accent: #BB2B10;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  color: #000000;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
  font-weight: inherit;
}
ul {
  list-style: none;
}
.container {
  max-width: 1440px;
  margin: 0 auto;
}
.nav {
  display: flex;
  padding: 20px 0px;
  align-items: center;
  justify-content: space-between;
}
.logo {
  display: flex;
  align-items: center;
}
.logo__img {
  width: 45px;
  height: 45px;
  margin-right: 8px;
}
.logo__text {
  font: 700 24px 'Poppins', sans-serif;
}
.nav__list {
  display: flex;
  gap: 75px;
  padding: 0;
  margin-right: 20px;
}
.nav__list-item {
  font-size: 14px;
  font-weight: 600;
  position: relative;
}
.nav__list-item::before {
  content: '';
  width: 0px;
  height: 3px;
  box-shadow: 3px 1px 4px rgba(0, 0, 0, 0.25);
  background-color: var(--color-accent);
  position: absolute;
  bottom: -7px;
  left: 50%;
  transform: translateX(-50%);
  transition: width .3s ease;
}
.nav__list-item:hover::before {
  width: 41px;
}

.nav__account {
  display: flex;
  align-items: center;
}
.basket {
  width: 16px;
  height: 16px;
  
  margin-right: 30px;
}
.button {
  padding: 5px 14px;
  border-radius: 11px;
  background: var(--color-accent);
  color: #FFFFFF;
  font-weight: 700;
  font-size: 13px;
}
.button-login {
  display: flex;
  align-items: center;
  padding-right: 23px;
}
.button-login svg {
  margin-right: 5px;
}

/* main */

.main {
  display: flex;
  align-items: center;
}

.main__title {
  font-weight: 700;
  font-size: 67px;
  color: #010F1C;
  line-height: 80px;
  margin: 0;
  max-width: 600px;
}

.main__title span {
  color: var(--color-accent);
  font-family: "LobsterTwo", sans-serif;
  font-size: 96px;
}

.main__text {
  color: #333333;
  font-weight: 500;
  font-size: 18px;
  line-height: 30px;
  max-width: 450px;
  margin: 25px 0;
}

.button-order {
  padding: 10px 45px;
  border-radius: 93px;
  font-weight: 700;
  font-size: 20px;
}

.main__image {
  margin-right: -100px;
  margin-left: 200px;
}

.top {
  background: rgba(227, 214, 190, 0.29);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 80px;
  padding: 25px 0 62px;
}

.top__header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top__title{
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  font-size: 40px;
}

.top__control {
  display: flex;
}

.top__control-item {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background: #FFFFFF;
  border: 4px solid #DC8C2A;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .3s ease-in-out;
  cursor: pointer;
}

.top__control-item:hover{
  background-color: #DC8C2A;
}

.top__control-left {
  margin-right: 20px;
}

.top__control-left img {
  transform: rotate(180deg);
}

.top__cards {
  display: flex;
  justify-content: space-between;
  padding-top: 81px;
}



.top__cards-item {
  background-color: white;
  border-radius: 5px 45px 0 45px;
  width: 290px;
  height:350px;
}

.top__cards-item h1{
  font-size: 25px;
  display:inline-block;
  margin-left: 27px;
}

.top__cards-item h2{
  font-size: 19px;
  display:inline-block;
}

.top__cards-item-text {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 16px;
  padding-top: 7px;
}

.top__cards-item span {
  color: #DC8C2A;
}

.mark{
  margin-top: 9px;
  margin-left: 29px;
  font-size: 13px;
  color: gray;
  display: flex;
}

.mark img {
  margin-left:4px;
}


.card_1{
  border-radius: 5px 45px 0 45px;
}

.stars img {
  border-radius: 0;
  margin-left: 29px;
  margin-top: 26px;
}

.button_plus {
  float:right;
  background-color: #DC8C2A;
  color: #fff;
  width: 90px;
  height: 74px;
  border-radius:42px 0 0 0;
  position: relative;
  bottom:53px;
  text-align: center;
}

.about_text {
  text-align: center;
  margin-top: 120px;
}

.about_title {
  color: #BB2B10;
  font-size: 21px;
  font-weight: 800;
  letter-spacing: 0.16em;
  margin-bottom: 12px;
}

.about_subtitle {
  font-size: 45px;
  color: #010F1C;
  font-family:Arial, Helvetica, sans-serif;
  font-weight: 800;
}

.cards {
  display: flex;
  justify-content: space-evenly;
}

.about_cards {
  display: flex;
  margin-top:50px;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.cards_title {
  font-size: 28px;
  margin-top: 10px;
}

.cards_subtitle {
  font-size: 18px;
  font-weight: normal;
  margin-top: 17px;
  text-align: center;
}

footer {
  margin-top:150px;
  background-color: #F0EEEC;
  display:flex;
  justify-content: space-evenly;
}

.first_section {
  margin-top: 105px;
  margin-bottom:140px;
}

.footer_number {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 600;
}

.footer_mail {
  font-size: 16px;
  font-weight: 600;
}

.second_section {
  margin-right: 125px;
}

.third_section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 77px;
}

.nav_links {
  font-size: 18px;
  font-weight: 600;
  margin:10px 0;
}

.footer_about {
  margin-bottom: 40px;
}

.footer_devis {
  margin-top: 30px;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  justify-content: center;
  align-items: center;
  line-height: 30px;
}

.socials{
  margin:30px 0;
  justify-content: space-around;
  display: flex;
}

.logo_footer {
  padding-top: 15px;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
}

